export const period_years = [];
for (let i = 1; i <= 9; i++) {
  period_years.push({
    value: `period_year${i}`,
    label: `${i}`,
  });
}
period_years.unshift({
  value: "periodYearAll",
  label: " ",
});
period_years.push({
  value: "period_year10",
  label: "10 ",
});

