import "../css/nav.css";
import { useNavigate } from "react-router-dom";
const Nav = () => {
  const navigate = useNavigate();

  const goPage = (path) => {
    navigate(path);
  };
  return (
    <div className="nav">
      <ul>
        <li
          onClick={() => {
            goPage("/matchingInfo");
          }}
        >
          
        </li>
        <li
          onClick={() => {
            goPage("/process");
          }}
        >
          
        </li>
        <li
          onClick={() => {
            goPage("/eduProgram");
          }}
        >
          
        </li>
        <li
          onClick={() => {
            goPage("/square");
          }}
        >
          
        </li>
      </ul>
      <ul>
        <li></li>
        <li></li>
      </ul>
    </div>
  );
};

export default Nav;
